import './header.css';
import React from 'react';
import Typed from 'typed.js';
import { useEffect, useRef } from 'react';

import { data } from '../../containers/technologies/technologiesData';

export default function App() {
  const el = useRef(null);

  useEffect(() => {
    const typed = new Typed(el.current, {
      strings: data.map((string) => string.title),
      startDelay: 300,
      typeSpeed: 100,
      backSpeed: 100,
      backDelay: 100,
      smartBackspace: true,
      loop: true,
      showCursor: false,
    });

    return () => {
      typed.destroy();
    };
  }, []);

  return (
    <section id='header'>
      <div className='header-wraper'>
        <div className='main-info'>
          <h1 className='gradient__text'>React Front-end developer</h1>
          <span className='type-text' ref={el}></span>
          <a href='#contact' className='btn-main'>
            kontakt
          </a>
        </div>
      </div>
    </section>
  );
}
