import React, { useState } from 'react';
import logo from './logo.png';
import './navbar.css';
import Darkmode from '../darkmode/Darkmode';

const Navbar = () => {
  const [navbar, setNavbar] = useState(false);
  const changeBacground = () => {
    if (window.scrollY >= window.innerHeight - 84) {
      setNavbar(true);
    } else {
      setNavbar(false);
    }
  };

  window.addEventListener('scroll', changeBacground);

  let [isMobile, setIsMobile] = useState(false);

  {
    isMobile ? (document.body.style.overflow = 'hidden') : (document.body.style.overflow = 'auto');
  }

  return (
    <nav className={navbar ? 'navbar-expand-lg navbar active' : 'navbar-expand-lg navbar'}>
      <div className='container'>
        <a className='navbar-brand' href='#header'>
          <div className='logo' src={logo} alt='logo'></div>
        </a>

        <div className='box'>
          <div
            className={isMobile ? 'hamburger navbar-toggler active' : 'hamburger navbar-toggler not-active '}
            type='button'
            data-bs-toggle='collapse'
            data-bs-target='#navbarSupportedContent'
            aria-controls='navbarSupportedContent'
            aria-expanded='false'
            aria-label='Toggle navigation'
            onClick={() => setIsMobile(!isMobile)}
          >
            <span></span>
            <span></span>
            <span></span>
          </div>
        </div>

        <div
          id='navbarSupportedContent'
          className={isMobile ? 'collapse navbar-collapse nav-links-mobile' : 'collapse navbar-collapse nav-links'}
        >
          <ul className='navbar-nav ms-auto mb-lg-0' id='navbarToggle' onClick={() => setIsMobile(false)}>
            <li className='nav-item active'>
              <a className='nav-link' aria-current='page' href='#header'>
                Home
              </a>
            </li>
            <li className='nav-item'>
              <a className='nav-link' href='#about'>
                o mě
              </a>
            </li>
            <li className='nav-item'>
              <a className='nav-link' href='#technologies'>
                technologie
              </a>
            </li>
            <li className='nav-item'>
              <a className='nav-link' href='#portfolio'>
                portfolio
              </a>
            </li>
            <li className='nav-item'>
              <a className='nav-link' href='#contact'>
                kontakt
              </a>
            </li>
            <li>
              <Darkmode />
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
