import React from 'react';
import { useInView } from 'react-intersection-observer';

import portfolioData from './portfolioData';

import './portfolio.css';

export const Portfolio = React.forwardRef((props, ref) => {
  const { ref: row0Ref, inView: row0IsVisible } = useInView({ triggerOnce: true, delay: 0 });
  const { ref: row1Ref, inView: row1IsVisible } = useInView({ triggerOnce: true, delay: 50 });
  const { ref: row2Ref, inView: row2IsVisible } = useInView({ triggerOnce: true, delay: 200 });
  const { ref: row3Ref, inView: row3IsVisible } = useInView({ triggerOnce: true, delay: 250 });

  const observers = [
    { ref: row0Ref, visible: row0IsVisible },
    { ref: row1Ref, visible: row1IsVisible },
    { ref: row2Ref, visible: row2IsVisible },
    { ref: row3Ref, visible: row3IsVisible },
  ];

  return (
    <section id='portfolio' ref={ref}>
      <div className={`${props.visible ? 'section__shown' : 'section__hidden'}`}>
        {portfolioData.map((item, i) => (
          <div className='container section__padding' key={i}>
            {i === 0 && <h1 className='gradient__text portfolio__h1'>Portfolio</h1>}
            <div className={`row ${observers[i].visible ? 'row__shown' : 'row__hidden'}`} ref={observers[i].ref}>
              <div className='col-md portfolio__img'>
                <a href={item.href} target='_blank' rel='noreferrer'>
                  <img className='zoom' src={item.src} alt={item.alt} />
                </a>
              </div>
              <div className='col-md portfolio__text'>
                <h3 className='gradient__text'>{item.title}</h3>
                {item.content}
                <a href={item.href} target='_blank' rel='noreferrer' className='btn-main'>
                  zobraz
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </section>
  );
});

export default Portfolio;
