import { useInView } from 'react-intersection-observer';

import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import Navbar from './components/navbar/Navbar';
import Header from './components/header/Header';
import Arrow from './components/arrow/Arrow';
import Technologies from './containers/technologies/Technologies';
import About from './containers/about/About';
import Portfolio from './containers/portfolio/Portfolio';
import Contactform from './components/contactform/Contactform';

const App = () => {
  const { ref: aboutRef, inView: aboutIsVisible } = useInView({ triggerOnce: true });
  const { ref: portfolioRef, inView: portfolioIsVisible } = useInView({ triggerOnce: true });

  return (
    <>
      <Navbar />
      <Header />
      <About ref={aboutRef} visible={aboutIsVisible} />
      <Technologies />
      <Portfolio ref={portfolioRef} visible={portfolioIsVisible} />
      <Contactform />
      <Arrow />
    </>
  );
};

export default App;
