import "./darkmode.css";
import { ChangeEventHandler } from "react";

const Darkmode = () => {
  // const moonSun = document.querySelector("#moonsun");

  const setLight = () => {
    localStorage.setItem("theme", "light");
    document.documentElement.setAttribute("data-theme", "light");
    document.querySelector("#moonsun").classList.remove("fa-sun");
    document.querySelector("#moonsun").classList.add("fa-moon");
  };

  const setDark = () => {
    localStorage.setItem("theme", "dark");
    document.documentElement.setAttribute("data-theme", "dark");
    document.querySelector("#moonsun").classList.remove("fa-moon");
    document.querySelector("#moonsun").classList.add("fa-sun");
  };

  // const storedTheme = localStorage.getItem("theme");

  // const prefersDark =
  //   window.matchMedia &&
  //   window.matchMedia("(prefers-color-scheme: dark)").matches;

  // const defaultDark =
  //   storedTheme === "dark" || (storedTheme === null && prefersDark);

  // if (defaultDark) {
  //   setLight();
  // }

  const toggleTheme: ChangeEventHandler<HTMLInputElement> = (e) => {
    if (e.target.checked) {
      setLight();
    } else {
      setDark();
    }
  };
  return (
    <div className="darkmode__toggle-theme-wrapper">
      <label className="darkmode__toggle-theme" htmlFor="checkbox">
        <input
          type="checkbox"
          id="checkbox"
          onChange={toggleTheme}
          // defaultChecked={defaultDark}
        />
        <div className="darkmode__toggle">
          <i className="fa fa-5 fa-sun" aria-hidden="true" id="moonsun"></i>
        </div>
      </label>
    </div>
  );
};

export default Darkmode;
