import Slider from 'react-slick';
import { data } from './technologiesData';

import './technologies.css';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';

const Technologies = () => {
  const settings = {
    autoplay: true,
    infinite: true,
    slidesToShow: 5,
    slidesToScroll: 1,
    autoplaySpeed: 2500,
    speed: 1000,
    dots: true,
    arrows: false,
    responsive: [
      {
        breakpoint: 992,
        settings: {
          slidesToShow: 4,
          slidesToScroll: 2,
        },
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <section className='section__background' id='technologies'>
      <div className='container section__padding'>
        <h1 className='gradient__text portfolio__h1'>Technologie</h1>
        <div className='row'>
          <Slider {...settings}>
            {data.map((item, index) => (
              <div key={index} className='col'>
                <img src={item.img} alt={item.alt} title={item.title} />
              </div>
            ))}
          </Slider>
        </div>
      </div>
    </section>
  );
};

export default Technologies;
