import './contactform.css';
import React, { useRef, useState } from 'react';
import emailjs from '@emailjs/browser';
import Customalert from '../alert/Customalert';

const Contactform = () => {
  // alert
  const [show, setShow] = useState(false);
  const [loader, setLoader] = useState(false);
  const emailSuccessMessage = 'E-mail byl úspěšně odeslán.';

  // contactfrom
  const form = useRef();

  const sendEmail = async (e) => {
    e.preventDefault();
    setLoader(true);

    await emailjs.sendForm('service_3dmhw2f', 'template_tkk09bd', form.current, 'pND9L3UoPHur0a2qH').then(
      (result) => {
        setShow(true);
        console.log(result.text);
        setTimeout(() => {
          setShow(false);
        }, 6000);
      },
      (error) => {
        console.log(error.text);
      }
    );
    setLoader(false);
    form.current.reset();
  };

  return (
    <section className='section__background' id='contact'>
      <div className='container section__padding'>
        <h1 className='gradient__text portfolio__h1'>Kontakt</h1>
        <div className='col-sm-6 m-auto'>
          <form className='contact__form' ref={form} onSubmit={sendEmail}>
            <div className='row'>
              <div className='col-12 col-lg-6 mb-4'>
                <input className='form-control contactform__background' type='text' name='name' placeholder='Jméno' required />
              </div>
              <div className='col-12 col-lg-6 mb-4'>
                <input className='form-control contactform__background' type='email' name='email' placeholder='E-mail' required />
              </div>
              <div className='col-12 mb-4'>
                <textarea
                  className='form-control contactform__background'
                  name='message'
                  rows='5'
                  placeholder='Vaše zpráva...'
                  required
                />
              </div>
              <div className='col-12 m-auto contactform__submit mb-3'>
                <button className='btn-main mb-5' type='submit' value='Odeslat' placeholder='odeslat'>
                  {loader ? <i className='fa fa-circle-o-notch fa-spin buttonload' id='loader'></i> : 'odeslat'}
                </button>
              </div>
            </div>
          </form>
          <div className='contactform__social'>
            <a href='https://github.com/adamko06' target='_blank' rel='noreferrer'>
              <i className='fa-brands fa-github'></i>
            </a>
            <a href='https://www.linkedin.com/in/adam-ko%C5%BEu%C5%A1n%C3%ADk-60a69b224/' target='_blank' rel='noreferrer'>
              <i className='fa-brands fa-linkedin'></i>
            </a>
          </div>
        </div>
        {show ? <Customalert emailSuccess={emailSuccessMessage} /> : ''}
      </div>
    </section>
  );
};

export default Contactform;
